

















































import Vue from 'vue';

export default Vue.extend({
  name: 'About',
  data() {
    return {
      i: 0,
      isExpanded: false,
    };
  },
  methods: {
    img(p: number) {
      let c: number = this.i + p;
      while (c < 0) { c += 6; }
      return c % 6 || 6;
    },
  },
});
