






























































































































import Vue from 'vue';

export default Vue.extend({
  name: 'Header',
  data() {
    return {
      locale: this.$i18n.locale,
      sidebar: false,
      langs: [
        { code: 'ru', title: 'Русcкий' },
        { code: 'en', title: 'English' },
      ],
    };
  },
  watch: {
    locale(val) {
      this.$i18n.locale = val;
    },
  },
});
