






import Vue from 'vue';

export default Vue.extend({
  name: 'ToTop',
  data() {
    return {
      visible: false,
    };
  },
  methods: {
    go() {
      document.body.scrollTop = 0;
      document.documentElement.scrollTop = 0;
    },
  },
  mounted() {
    window.onscroll = () => {
      if (document.body.scrollTop > 20 || document.documentElement.scrollTop > 20) {
        this.visible = true;
      } else {
        this.visible = false;
      }
    };
  },
});
